<template>
  <!--此页面用于，借API接口请求数据-->
    <section>
      <ul v-for="SuperMan in SuperMans" class="pp">
<!--城市-->
        <li >城市: {{SuperMan.city}}</li>
        <!--用户名-->
        <li v-text="SuperMan.user_name">用户名:</li>
        <!--头像-->
        <li >头像: {{SuperMan.avatar}}</li>
        <!--id-->
        <li v-text="SuperMan.id">id:</li>
        <!--注册时间-->
        <li v-text="SuperMan.create_time">注册时间: </li>
        <!--管理标识-->
        <li v-text="SuperMan.admin">管理标识: </li>
        <!---->
        <li v-text="SuperMan.__v"></li>
        <!---->
        <li v-text="SuperMan.status"></li>
      </ul>
    </section>
</template>

<script>
  import  Vue from 'vue'
    export default {
        name: "AskFor",
      data:function () {
          return {
            //接收用户名
             username:'',
               //所在地
            city:'',
            //头像
            avatar:'',
            //存储管理员的数组
             SuperMans:[]

          }
      },
      created(){
          Vue.axios.get("https://elm.cangdu.org/admin/all").then (res =>{
            this.SuperMans = res.data.data;
            //打印一下
            console.log(this.SuperMans);
          })
 // console.log(admins.data);
        }
      }

</script>

<style scoped>

  .pp{
    text-align: center;
  }
</style>



