<template>
  <!--请求数据到网页中显示，以li的标签形式展示。-->
    <section>
      <ul v-for="li in lis">
        <li> {{li.category}}</li>
        <li>{{li.delivery_mode.text}}</li>
        <li>{{li.description}}</li>
        <li>{{li.distance}}</li>
        <li><img src="" alt="">{{li.image_path}}</li>
        <li>{{li.description}}</li>
        <li>{{li.description}}</li>
        <li>{{li.description}}</li>
      </ul>
    </section>
</template>

<script>
  import Vue from 'vue'

    export default {
        name: "Change",
      data : function () {
        return {
          lis:[] //定义一个数组
        }
      },
      created () {
         Vue.axios.get('https://elm.cangdu.org/v4/restaurants?geohash=31.22967,121.4762&keyword=肯德基').then (res=>{
           //用lis 接收返回的数据
           this.lis = res.data;
           //打印接收到的数据
           console.log(this.lis);
         })
      }
    }
</script>

<style scoped>

</style>



